/* ---------------------------------------------------------------------

BASE > BUTTONS

--------------------------------------------------------------------- */
.tk-stl-button {
  display: inline-block;
  zoom: 1;
  line-height: normal;
  white-space: nowrap;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  outline: 0;
  font-family: inherit;
  font-size: 100%;
  text-decoration: none;
  padding: 0.5em 1em 0.5em 1em;
  border: 0;
  /*IE 6/7/8*/
  border: 1px solid rgba(0, 0, 0, 0.13);
  background: #3b59a0;
  color: #fff;
  border-radius: 3px;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  -webkit-appearance: none;
}
.tk-stl-button::-moz-focus-inner {
  padding: 0;
  border: 0;
}
.tk-stl-button.visited,
.tk-stl-button:visited {
  color: #fff;
}
.tk-stl-button.tk-stl-button-hover,
.tk-stl-button:hover,
.tk-stl-button:focus {
  background: #3e69d0;
  text-decoration: none;
}
.tk-stl-button.tk-stl-button-secondary {
  background: #e3e3e3;
  color: #a6a6a6;
}
.tk-stl-button.tk-stl-button-secondary:hover,
.tk-stl-button.tk-stl-button-secondary:focus {
  background: #cecfcf;
}
.tk-stl-button.tk-stl-button-green {
  background: #60cc5d;
  color: #fff;
  border-color: #60cc5d;
}
.tk-stl-button.tk-stl-button-green:hover {
  background: #3ebb3b;
  border-color: #3ebb3b;
}
.tk-stl-button.tk-stl-button-blue {
  background: #1b8ff2;
  color: #fff;
  border-color: #1b8ff2;
}
.tk-stl-button.tk-stl-button-blue:hover {
  background: #0c75ce;
  border-color: #0c75ce;
}
.tk-stl-button.tk-stl-button-white {
  color: #595959;
  border-color: #fff;
  background: rgba(255, 255, 255, 0.05);
}
.tk-stl-button.tk-stl-button-white:hover {
  border-color: #fff;
  background: rgba(255, 255, 255, 0.2);
}
.tk-stl-button.tk-stl-button-small {
  padding: 0.5em 0.75em;
  border-width: 1px;
  font-weight: normal;
  font-style: normal;
  font-size: 0.8em;
}
.tk-stl-button[disabled],
.tk-stl-button-disabled,
.tk-stl-button-disabled:hover,
.tk-stl-button-disabled:focus,
.tk-stl-button-disabled:active {
  border: none;
  cursor: not-allowed;
  box-shadow: none;
  background: #aaa;
}
.tk-stl-button-hidden {
  display: none;
}
/* Downward Arrow Button (span) */
.caret {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 0;
  vertical-align: middle;
  border-top: 4px solid;
  border-right: 4px solid transparent;
  border-left: 4px solid transparent;
}
.tk-stl-button.tk-stl-block {
  width: 100%;
  border-radius: 0;
  border: none;
}
