/* A horizontal panel that flags an alert to the user.  */
/*
<div class="tk-stl-alertbox tk-stl-alertbox-red">
    <p>Did you know you can also search for <a href="#">towns</a>?</p>
</div>
*/
.tk-stl-alertbox {
  position: relative;
  transition: opacity 300ms ease-out, background 300ms ease-out;
  margin: 0 0 1em 0;
  padding: 0.75em;
  font-size: 0.8em;
  border-width: 1px;
  border-style: solid;
  border-color: rgba(0, 0, 0, 0.08);
  border-radius: 3px;
  background: #fff;
  /* Variations */
}
.tk-stl-alertbox p {
  margin: 0;
  padding: 0;
}
.tk-stl-alertbox .close {
  right: 0.05em;
  top: 0;
  bottom: 0;
  background: none;
  color: rgba(0, 0, 0, 0.26);
  font-size: 1.25em;
  padding: 0.55em;
  position: absolute;
  margin-top: -1px;
}
.tk-stl-alertbox.tk-stl-alertbox-secondary {
  background: rgba(0, 0, 0, 0.04);
}
.tk-stl-alertbox.tk-stl-alertbox-positive {
  background: #00d46a;
  color: #fff;
}
.tk-stl-alertbox.tk-stl-alertbox-negative {
  background: #fc5a5d;
  color: #fff;
}
.tk-stl-alertbox.tk-stl-alertbox-warning {
  background: #fec03b;
  color: #fff;
}
.tk-stl-alertbox.tk-stl-alertbox-info {
  background: #3f9ff6;
  color: #fff;
}
.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.animate-fadeInUp {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: fadeInUp;
  animation-name: fadeInUp;
}
.animate-fadeInDown {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}
@-webkit-keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 20px, 0);
    transform: translate3d(0, 20px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
@keyframes fadeInUp {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, 20px, 0);
    transform: translate3d(0, 20px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
@-webkit-keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -20px, 0);
    transform: translate3d(0, -20px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
@keyframes fadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -20px, 0);
    transform: translate3d(0, -20px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}
.tk-stl-sortoptions-group.show ul,
.saved-searches,
.bookmarks-list {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
  -webkit-animation-duration: 0.2s;
  animation-duration: 0.2s;
}
