.tbl-wrapper {
  margin: 0 0 5em 0;
  padding: 0;
}
table {
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
  border: 0px solid #e0e0e0;
  font-size: 0.9em;
}
table th {
  border-bottom: 1px solid #e0e0e0;
  color: #333;
}
table th,
table td {
  padding: 0.5em 0.7em;
  text-align: left;
  vertical-align: top;
}
table tbody tr:nth-child(odd) td {
  background-color: #fafafa;
}
table th,
table td {
  border-bottom: 1px solid #e0e0e0;
}
