.tbl-compare table td:first-child,
.tbl-stacked-compare table th:first-child,
.tbl-stacked-compare table td:first-child {
  font-weight: 700;
}
/*    1 & 2 COMPARISON TABLE 	       */
/*-----------------------------------*/
@media only screen and (max-width: 800px) {
  .tbl-compare table:after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
  }
  .tbl-compare table {
    zoom: 1;
  }
  .tbl-compare table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    border: 1px solid #e0e0e0;
  }
  .tbl-compare table th,
  .tbl-compare table td {
    margin: 0;
    vertical-align: top;
  }
  .tbl-compare table th {
    text-align: left;
  }
  .tbl-compare table {
    display: block;
    position: relative;
    width: 100%;
  }
  .tbl-compare table thead {
    display: block;
    float: left;
  }
  .tbl-compare table tbody {
    display: block;
    width: auto;
    position: relative;
    overflow: hidden;
    overflow-x: auto;
    white-space: nowrap;
  }
  .tbl-compare table thead tr {
    display: block;
  }
  .tbl-compare table th {
    display: block;
    text-align: right;
  }
  .tbl-compare table tbody tr {
    display: inline-block;
    vertical-align: top;
    margin-right: -0.28em;
    /* stretchbg fix */
  }
  .tbl-compare table td {
    display: block;
    text-align: left;
  }
  /* borders */
  .tbl-compare table th {
    border-bottom: 0;
    border-left: 0;
  }
  .tbl-compare table td {
    border-left: 0;
    border-right: 0;
    border-bottom: 0;
  }
  .tbl-compare table tbody tr {
    border-left: 1px solid #e0e0e0;
