/*    3. STACKED                      */
/*------------------------------------*/
@media only screen and (max-width: 800px) {
  .tbl-stacked table,
  .tbl-stacked thead,
  .tbl-stacked tbody,
  .tbl-stacked th,
  .tbl-stacked td,
  .tbl-stacked tr {
    display: block;
  }
  .tbl-stacked table {
    border: 1px solid #e0e0e0;
    border-bottom: 0;
  }
  /* Accessibly hide table headers */
  .tbl-stacked thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  .tbl-stacked tr {
    border-bottom: 1px solid #e0e0e0;
  }
  .tbl-stacked td {
    /* as if row */
    border: none;
    position: relative;
    padding-left: 50%;
    white-space: normal;
    text-align: left;
  }
  .tbl-stacked td:last-child {
    border-bottom: 0;
  }
  .tbl-stacked td:before {
    position: absolute;
    /* as if header */
    top: 6px;
    /* faux padding */
    left: 10px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    text-align: left;
    font-weight: 700;
  }
  /* Labels */
  .tbl-stacked td:before {
    content: attr(data-title);
    color: #333;
    line-height: 1.5;
  }
}
