  margin: 0 0 0.875em 0;
  padding: 0;
}
ul.facet-list li a {
  margin: 0;
}
ul.facet-list li .metadata {
  display: block;
  float: right;
  width: 33%;
  margin-left: -35%;
  margin-top: 2px;
  text-align: right;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  background-color: #fff;
  line-height: 1.25em;
}
ul.facet-list li .metadata .count {
  position: relative;
  float: right;
  overflow: hidden;
  padding: 0 10%;
  -webkit-border-radius: 3px;
