}
ul.facet-list li .metadata .count {
  float: right;
  overflow: hidden;
  padding: 0 10%;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  background-color: #f5f8f9;
  color: #595959;
  font-size: 0.750em;
}
