.tk-stl-timeline {
  padding: 0;
  margin: 0;
  font-size: 0.9em;
  list-style: none;
}
.tk-stl-timeline.vertical {
  display: table;
}
.tk-stl-timeline.vertical .time-slot {
  display: table-row;
  position: relative;
}
.tk-stl-timeline.vertical .time-slot .milestone {
  padding: 0 1em 0 0;
  font-size: 1.1em;
  text-align: right;
  display: table-cell;
  width: auto;
}
.tk-stl-timeline.vertical .time-slot .events {
  margin-left: 1em;
  display: table-cell;
}
.tk-stl-timeline.vertical .time-slot .events .entry {
  position: relative;
  padding: 0 1em 2.5em 1em;
}
.tk-stl-timeline.vertical .time-slot .events .entry:before {
  content: '';
  width: 2px;
  position: absolute;
  top: 10px;
  bottom: 10px;
  left: 0;
  z-index: 1;
  background: #e6e6e6;
  height: 100%;
}
.tk-stl-timeline.vertical .time-slot .events .entry:after {
  content: '';
  width: 6px;
  height: 6px;
  background: #b6b6b6;
  border: 2px solid #fff;
  border-radius: 50%;
  position: absolute;
  left: -4px;
  top: 5px;
  z-index: 2;
}
.tk-stl-timeline.vertical .time-slot .events .entry .metadata {
  font-size: 0.9em;
  color: #A2A2A2;
}
.tk-stl-timeline.vertical .time-slot:last-child .entry:last-child {
  padding-bottom: 0em;
}
.tk-stl-timeline.horizontal {
  position: relative;
  overflow-x: scroll;
  width: 100%;
  white-space: nowrap;
}
.tk-stl-timeline.horizontal .time-slot {
  position: relative;
  display: inline-block;
  margin-right: -3px;
}
.tk-stl-timeline.horizontal .time-slot .milestone {
  padding: 0 1em;
  font-size: 1.1em;
  width: 100%;
}
.tk-stl-timeline.horizontal .time-slot .events {
  margin-top: 1em;
  display: inline-flex;
}
.tk-stl-timeline.horizontal .time-slot .events .entry {
  position: relative;
  padding: 0 1em 1em 1em;
  float: left;
  text-align: center;
}
.tk-stl-timeline.horizontal .time-slot .events .entry:before {
  content: ' ';
  height: 2px;
  position: absolute;
  top: -0.6em;
  bottom: 10px;
  left: 0;
  z-index: 1;
  background: #e6e6e6;
  width: 100%;
}
.tk-stl-timeline.horizontal .time-slot .events .entry:after {
  content: ' ';
  width: 6px;
  height: 6px;
  background: #b6b6b6;
  border: 2px solid #fff;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: -0.9em;
  z-index: 2;
}
.tk-stl-timeline.horizontal .time-slot .events .entry .metadata {
  font-size: 0.9em;
  color: #A2A2A2;
}
.tk-stl-timeline-container {
  position: relative;
  overflow: auto;
}
.tk-stl-timeline-container.fade-left:before {
  content: ' ';
  background: -moz-linear-gradient(left, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
  background: -webkit-linear-gradient(left, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
  background: linear-gradient(to right, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffffff', endColorstr='#00ffffff', GradientType=1);
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 5em;
  z-index: 1;
}
.tk-stl-timeline-container.fade-right:after {
  content: ' ';
  background: -moz-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.99) 99%, #ffffff 100%);
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.99) 99%, #ffffff 100%);
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.99) 99%, #ffffff 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffffff', endColorstr='#ffffff', GradientType=1);
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 5em;
  z-index: 1;
}
